import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	data() {
		return {
			id:'',
			isFinish: false,
		    info: {},
		    pic: [], // 预览图片列表
		    objection: '', // 拒绝理由
		    user_list: [], // 执委团队
		    my_user_id:'', // 我的user_id
		    apply_user_id: 0, // 发布者user_id
		    btn_status: 0, // 底部按钮状态  0未操作 1已同意 2已拒绝 3我是发布者
		    refuse_popup: false, // 拒绝弹窗
		    reason: '' // 拒绝理由
		}
	},
	onShow() {
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		this.getData()
	},
	onLoad(e) {
		
		this.my_user_id = uni.getStorageSync("userId")
		this.id = e.id
		
	},
	methods: {
		// 取消审批
		cancel () {
			Dialog.confirm({
			  title: '提示',
			  message: '确定要取消吗？'
			}).then(() => {
				this.$api.cancel_approval({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId"),
					id:this.id
				}).then(res=>{
					if(res.data.code=="200"){
						uni.showToast({
						    title: '取消成功',
						    mask: "true",
							icon:'none'
						});
						this.getData()
					}else{
						uni.showToast({
						    title: res.data.msg,
						    mask: "true",
							icon:'none'
						});
					}
				})
			}).catch(() => {
			  // on cancel
			});
		},
		// 同意和重新同意
		agree () {
			uni.navigateTo({
				url:'../../pagesThree/Sign/Sign?id='+this.id
			})
		},
		// 确认拒绝
		confirm_refuse(){
			if (this.reason.trim().length > 0 && this.reason.length > 0) {
			        this.$api.Clubmanage_refuse({
			          id: this.id,
			          token: uni.getStorageSync("token"),
			          user_id: uni.getStorageSync("userId"),
			          reason: this.reason
			        }).then(res => {
			          if (res.data.code === '200') {
			            uni.showToast({
			                title: '操作成功',
			                mask: "true",
			            	icon:'none'
			            });
			            this.refuse_popup = false
			            this.getData()
			          } else {
			            uni.showToast({
			                title: res.data.msg,
			                mask: "true",
			            	icon:'none'
			            });
			          }
			        })
			      } else {
					uni.showToast({
					    title: '请输入拒绝理由',
					    mask: "true",
						icon:'none'
					});
			      }
		},
		// 关闭拒绝理由弹窗
		onClose(){
			this.refuse_popup = false
		},
		// 拒绝理由
		message(title){
			Dialog.alert({
			  title: '拒绝理由',
			  message: title
			}).then(() => {
			  // on close
			});
		},
		// 图片预览
		imagePreview(url){
			uni.previewImage({
				urls:[url]
			})
		},
		// 签名预览
		signPreview(url){
			uni.previewImage({
				urls:[url]
			})
		},
		getData(){
			this.$api.fund_detail({
			        token: uni.getStorageSync("token"),
			        user_id: uni.getStorageSync("userId"),
			        id: this.id
			      }).then(res => {
					 uni.hideLoading();
			        if (res.data.code === '200') {
			          this.info = res.data.data
			          let arr = []
			          if (this.info.images) {
			            for (let i = 0; i < this.info.images.length; i++) {
			              arr.push(this.info.images[i].url_image)
			            }
			            this.pic = arr
			          }
			          if (this.info.status === 0) {
			            this.user_list = this.info.commissioner_team
			            if (Number(this.my_user_id) === this.info.user_id) { // 我是发布者
			              this.btn_status = 3
			            } else {
			              for (let i = 0; i < this.user_list.length; i++) {
			                if (Number(this.my_user_id) === this.user_list[i].user_id) {
			                  this.btn_status = this.user_list[i].status
			                }
			              }
			            }
			          } else if (this.info.status === 3) { // 审批拒绝
			            this.objection = this.info.objection
						console.log(this.info)
			          }
			          this.$nextTick(() => {
			            this.isFinish = true
			          })
			        }
			      })
		}
	}
}
